import React from 'react'
import { fetch, API } from '../../services/';
function CardGamertag({ spartan }) {
    let emblemaUrl;

    const getEmblem = () => {
        API.getEmblem.url = API.getEmblem.url.replace("{player}", spartan.Gamertag)
        fetch(API.getEmblem).then((response) => {
            if (response.status === 200) {
                emblemaUrl = response.request.responseURL;
            } else {
                alert('dfsdfsd')
            }
        }).catch(function (error) {
            if (error.response && error.response.status === 401) {
                window.location.href = "logon";
            } else {
                // Handle error however you want
            }
        });
    }
    return (
        <div class="max-w-sm w-full lg:max-w-full lg:flex">
            <div class="h-48 lg:h-auto lg:w-48 flex-none bg-cover rounded-t lg:rounded-t-none lg:rounded-l text-center overflow-hidden" title="Woman holding a mug">
                <img src="" />
            </div>
            <div class="border-r border-b border-l border-gray-400 lg:border-l-0 lg:border-t lg:border-gray-400 bg-white rounded-b lg:rounded-b-none lg:rounded-r p-4 flex flex-col justify-between leading-normal">
                <div class="mb-8">
                    <div class="text-gray-900 font-bold text-xl mb-2">Gamertag</div>
                    <p class="text-gray-700 text-base">{spartan.Gamertag !== undefined ? spartan.Gamertag : ""}</p>
                    <div class="text-gray-900 font-bold text-xl mb-2">Clan</div>
                    <p class="text-gray-700 text-base">{spartan.Company.Name !== undefined ? spartan.Company.Name : ""}</p>
                    <div class="text-gray-900 font-bold text-xl mb-2">Tag</div>
                    <p class="text-gray-700 text-base">{spartan.ServiceTag !== undefined ? spartan.ServiceTag : ""}</p>
                    <button type="button" onClick={getEmblem}>traer emblema</button>
                </div>
            </div>
        </div>
    )
}

export default CardGamertag
